<script>
export let apiBase;
export let apikey;
export let demoImages;

let isSettingOpen = false;
let modal;
let apiBaseInput = '';
let apikeyInput = '';
let demoImagesInput = '';
let magicLink;
$: isJSONMalform = demoImagesInput.length > 0 && !isValidJSON(demoImagesInput);
$: magicLink = `${window.location.origin}/?base=${encodeURIComponent(apiBaseInput)}&key=${encodeURIComponent(apikeyInput)}`;

function dimmerClick(event) {
  if (modal.contains(event.target)) {
    return;
  }
  closeModal();
}

function openModal() {
  isSettingOpen = true;
  apiBaseInput = apiBase;
  apikeyInput = apikey;
  demoImagesInput = demoImages;
}

function closeModal() {
  isSettingOpen = false;
}

function saveSetting() {
  window.localStorage.setItem("apiBase", apiBaseInput);
  window.localStorage.setItem("apikey", apikeyInput);
  window.localStorage.setItem("demo-images", demoImagesInput);
  apiBase = apiBaseInput;
  apikey = apikeyInput;
  demoImages = demoImagesInput;
  closeModal();
}

function isValidJSON(str) {
  try {
    JSON.parse(str);
  } catch(e) {
    return false;
  }
  const json = JSON.parse(str);
  return Array.isArray(json) && json.length > 0;
}

function bodyKeyup(event) {
  if (isSettingOpen && event.keyCode === 27) {
    closeModal();
  }
}
</script>

<svelte:body on:keyup={bodyKeyup} />
<button id="setting-button" on:click|preventDefault={openModal}><i class="fas fa-cog"></i></button>
<div id="api-setting" class:on={isSettingOpen} on:click={dimmerClick}>
  <form class="block shadow-sm text-align-left" on:submit|preventDefault={saveSetting} bind:this={modal}>
    <label>
      API base
      <input tye="text" bind:value={apiBaseInput} placeholder="https://api.pxl.ai/v2">
    </label>
    <label>
      API key
      <input type="text" id="input_apikey" bind:value={apikeyInput}>
    </label>
    <label>
      Demo images
      <textarea bind:value={demoImagesInput} class:err={isJSONMalform} placeholder="[&quot;imageURL&quot;, &quot;imageURL&quot;, ... ]"></textarea>
    </label>
    <p class="magic-link">Magic link: {magicLink}</p>
    <input type="submit" value="Save" disabled={isJSONMalform}>
  </form>
</div>

<style>
#setting-button {
  position: absolute;
  top: 40px;
  right: 40px;
  border: 0 none;
  padding: 0;
  background: transparent;
  font-size: 20px;
  line-height: 33px;
  color: inherit;
}
#api-setting {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: auto;
  height: auto;
  background-color: rgba(0, 0, 0, 0.6);
  display: none;
  align-items: center;
  justify-content: center;
}
#api-setting.on {
  display: flex;
}
#api-setting form {
  width: 600px;
  background-color: #fff;
  max-height: 100%;
  overflow: auto;
}
#api-setting label {
  font-size: 14px;
  font-weight: 600;
}
#api-setting input,
#api-setting textarea {
  width: 100%;
  height: 40px;
  padding: 10px;
  margin-top: 10px;    
  margin-bottom: 20px;
  box-sizing: border-box;
  font-size: 14px;
  border: 1px solid #dcdcdc;
  border-radius: 0;
  -webkit-appearance: none;
  color: #555;
  font-weight: normal;
}
#api-setting textarea {
  height: 100px;
}
#api-setting textarea.err {
  border-color: #c00;
}
#api-setting input[type=submit] {
  margin-bottom: 0;
}
#api-setting .magic-link {
  font-size: 0.8em;
  color: #aaa;
}
</style>
