<script>
import { url } from '@sveltech/routify';

export let product;

function formatPrice(n) {
  return new Intl.NumberFormat(navigator.language, { style: 'currency', currency: 'KRW' }).format(n);
}
</script>

<div class="grid-item">
  <img class="thumbnail" src={product.image_url} decode="async" alt="">
  <a href={$url(`/view/?image_url=${encodeURIComponent(product.image_url)}`)}>
    <div class="view-button"><img src="/images/icon_search.svg" alt="Search"></div>
  </a>
  <div class="name"><a href="{product.product_url}">{product.name.replace(/<\/?[^>]+(>|$)/g, "")}</a></div>
  <div class="price"><a href="{product.product_url}">{formatPrice(product.price)}</a></div>
</div>

